/**
 * A basic content container component that adds the styles of a Material design card.
 *
 * While this component can be used alone, it also provides a number
 * of preset styles for common card sections, including:
 * - vdl-card-title
 * - vdl-card-subtitle
 * - vdl-card-content
 * - vdl-card-actions
 * - vdl-card-footer
 */
@Component({
  moduleId: module.id,
  selector: 'vdl-card',
  templateUrl: 'card.html',
  styleUrls: ['card.css'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    '[class.vdl-card]': 'true'
  }
})
export class VdlCard {}


/**
 * Component intended to be used within the `<vdl-card>` component. It adds styles for a
 * preset header section (i.e. a title, subtitle, and avatar layout).
 */
@Component({
  moduleId: module.id,
  selector: 'vdl-card-header',
  templateUrl: 'card-header.html',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    '[class.vdl-card-header]': 'true'
  }
})
export class VdlCardHeader {}


/**
 * Component intended to be used within the <vdl-card> component. It adds styles for a preset
 * layout that groups an image with a title section.
 */
@Component({
  moduleId: module.id,
  selector: 'vdl-card-title-group',
  templateUrl: 'card-title-group.html',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    '[class.vdl-card-title-group]': 'true'
  }
})
export class VdlCardTitleGroup {}


@NgModule({
  exports: [
    VdlCard,
    VdlCardHeader,
    VdlCardTitleGroup,
    VdlCardContent,
    VdlCardTitle,
    VdlCardSubtitle,
    VdlCardHeaderText,
    VdlCardHeaderContent,
    VdlCardHeaderActions,
    VdlCardActions,
    VdlCardFooter,
    VdlCardSmImage,
    VdlCardVdlImage,
    VdlCardLgImage,
    VdlCardImage,
    VdlCardXlImage,
    VdlCardAvatar,
  ],
  declarations: [
    VdlCard, VdlCardHeader, VdlCardTitleGroup, VdlCardContent, VdlCardTitle, VdlCardSubtitle,
    VdlCardActions, VdlCardFooter, VdlCardSmImage, VdlCardVdlImage, VdlCardLgImage, VdlCardImage,
    VdlCardXlImage, VdlCardAvatar, VdlCardHeaderText, VdlCardHeaderContent, VdlCardHeaderActions
  ],
})
export class VdlCardModule {
  /** @deprecated */
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: VdlCardModule,
      providers: []
    };
  }
}
